
.plant-page-popup  .popup-overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, 0.4);
    cursor: pointer;

}

.plant-page-popup .popup_inner {
    position: absolute;
    left: 30%;
    right: 30%;
    top: 20%;
    height: 75%;
    z-index: 2;

    margin: auto;
    background: white;
    border-radius: 5px;
    overflow-y: scroll;
    padding: 16px;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;

}

.plant-page-popup .popup_inner .plant-form {
    -ms-flex: 4 1;
        flex: 4 1;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 5px;
    border-top: 2px #99c8f1 solid;
    border-right: 2px #99c8f1 solid;
    border-left: 2px #99c8f1 solid;

    display: -ms-flexbox;

    display: flex;
}
.plant-page-popup .popup_inner .plant-form .plant-img {
    -ms-flex:2 1;
        flex:2 1;
    margin-top: 10px;
    max-width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.plant-page-popup .popup_inner .plant-form .plant-info {
    -ms-flex: 3 1;
        flex: 3 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-left: 20px;
}

.plant-page-popup .popup_inner .plant-form .plant-info .top .exit{
    -ms-flex: 0.2 1;
        flex: 0.2 1;
    float: right;
    color: rgba(0,0,0,0.6);
}
.plant-page-popup .popup_inner .plant-form .plant-info .top .exit:hover{
    color: rgba(244, 0, 0, 0.8);
}

.plant-page-popup .popup_inner .plant-form .plant-info .mid {
  padding-right: 20px;
    float: left;
}

.plant-page-popup .popup_inner .plant-form .plant-info .mid ,h2{
    -ms-flex:10 1;
        flex:10 1;
    padding-bottom: 8px;
}

.plant-page-popup .popup_inner .plant-form .plant-info .mid ,h4{
    -ms-flex:2 1;
        flex:2 1;
    padding-right:10px;
}

.plant-page-popup .popup_inner .plant-form .plant-info .bot .remove-plant{
    color: #cb3431;
    float: right;
    margin-right: 40px;
}

.plant-page-popup .popup_inner .plant-form .plant-info .title{
    -ms-flex:2 1;
        flex:2 1;
    font-weight: bold;
    color: #93c168;
    padding-left: 20px;
}

.plant-page-popup .popup_inner .plant-stats {
    -ms-flex: 3 1;
        flex: 3 1;
    padding-left: 5px;
    padding-right: 5px;
    padding-bottom: 5px;
    padding-top: 10px;
    border-right: 2px #99c8f1 solid;
    border-left: 2px #99c8f1 solid;
    border-bottom: 2px #99c8f1 solid;
    width: 100%;

    display: -ms-flexbox;

    display: flex;
}

.plant-page-popup .popup_inner .plant-stats .plant-stats-box-l {
    -ms-flex: 1 1;
        flex: 1 1;
    margin-right: 5px;
